import {
    MAP_INIT,
    CHANGED_MAP_SOURCE,
    MAPPING_INIT,
} from '../constants/Map';

const initialState = {
    source: [],
    width: 0,
    height: 0,
    mapping: {},
};

export default function page(state = initialState, action) {
    switch (action.type) {
        case MAP_INIT:{
            return {
                ...state,
                source: action.payload.source,
                width: action.payload.width,
                height: action.payload.height
            };
        }
        case MAPPING_INIT:
            return {
                ...state,
                mapping: action.payload
            };
        case CHANGED_MAP_SOURCE:
            return {...state, source: action.payload };
        default:
            return state;
    }
}